    .list {
        padding: 0;
        margin: 0;
    }
    
    .li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 20px;
        padding-right: 10px;
        height: 40px;
        list-style: none;
        margin-top: 15px;
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
        background-color: floralwhite;
    }
    
    .button {
        height: 20px;
    }