.container {
    width: 500px;
    padding: 30px;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    border: 1px solid burlywood;
    border-radius: 8px;
    background-color: whitesmoke;
}

.h1,
.h2 {
    text-align: center;
    margin: 0;
}